import { useState, useEffect } from "react";

function CounterDisplay({ count }) {
  // ... (Your CounterDisplay logic)
  let h2Elements = [];
  if (count > 0) {
    h2Elements = Array.from({ length: count }).map((_, index) => (
      <h2 key={index}>Count: {count}</h2>
    ));
  } else {
    h2Elements = Array.from({ length: -count }).map((_, index) => (
      <h2 key={index}>Count: {count}</h2>
    ));
    <h2 key={index}>Count: {count}</h2>;
  }
  return (
    <div>
      {h2Elements}
      {count > 0 && <p style={{ color: "green" }}>Count is positive!</p>}
    </div>
  );
}

export default function App() {
  const [count, setCount] = useState(0);

  // 1. 🟢 FIX: The useEffect is now correctly inside the component.
  useEffect(() => {
    // This code runs AFTER every render where 'count' has changed.
    document.title = `Counter: ${count}`;
  }, [count]); // 2. 🟢 FIX: We only need ONE useEffect for this simple task.

  function handleClick() {
    setCount(count + 1);
  }

  function subtract() {
    setCount(count - 1);
  }

  return (
    <div style={{ textAlign: "center", marginTop: "50px" }}>
      <h1>🚀 React Counter</h1>

      {/* 🛑 Remember to remove the redundant H2 here to only use the component display */}
      {/* <h2>Count: {count}</h2> */}

      <button onClick={handleClick}>Add One</button>
      <button onClick={subtract}>Minus One</button>
      <CounterDisplay count={count}></CounterDisplay>
    </div>
  );
}
